@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap');

body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

.wrapper{
    width: 600px;
    margin: 0 auto;

}

@media (max-width: 650px) {
    .wrapper{
        width: 400px;   
    }   
}

@media (max-width: 425px) {
	.wrapper{
        width: 275px;   
    } 
}