@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Georama:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap);
nav {
	display: flex;
	justify-content: space-around;
	background-color: rgb(44, 62, 80);
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}

a {
	margin: 10px 30px 10px 30px;
	font-size: 15px;
	text-decoration: none;
	text-transform: uppercase;
	color: rgb(255, 255, 255);
}

a.active {
	color: rgb(250, 150, 0);
	border-bottom: 2px solid rgb(250, 150, 0);
}

@media (max-width: 650px) {
	nav {
		flex-flow: row wrap;
		justify-content: center;
		align-items: center;
		padding-bottom: 5px;
	}

	a {
		margin: 8px 20px 8px 20px;
		width: 150px;
		text-align: center;
	}
}

@media (max-width: 425px) {
	nav {
		flex-flow: column nowrap;
	}

	a {
		margin: 6px 0 6px 0;
		width: auto;
	}
}

body {
    background: url(/cursor-hw19/static/media/main.f9541ed1.jpg) no-repeat center center fixed;
    background-size: cover;
}

.mainpage {
    margin-top: 165px;
    padding: 5px 0 0 0;
    font-family: 'Style Script', cursive;
    font-size: 36px;
    text-align: center;
    letter-spacing: 1px;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    border-radius: 20px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.717);
}

@media (max-width: 425px) {
    .mainpage {
        margin-top: 85px;
        margin-bottom: 85px;
        font-size: 30px;
        padding: 10px;
    }
}
.post {
    margin: 0 auto;
    margin-top: 20px;
    padding: 10px 15px 25px 15px;
    background-color: rgb(29, 44, 58);
    border: 1px solid rgb(109, 109, 109);
    border-radius: 20px;
    display: flex;
    flex-flow: column nowrap;
    font-family: 'Georama', sans-serif;
    color: rgb(255, 255, 255);
}

.post img, .post div {
    margin: 0 10px 0 10px;
}

.post-body {
    display: flex;
}

.author-photo {
    width: 10%;
    height: 10%;
    border-radius: 50%;
}

.article {
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    line-height: 28px;
}

.author-name {
    font-size: 16px;
    font-weight: 600;
}

.author-nickname, .date {
    font-weight: 600;
    font-size: 14px;
    color: rgba(137, 137, 137, 0.814);
}
 
.post-text {
    font-size: 15px;
}

.article .post-photo {
    width: 100%;
    margin: 12px 0 12px 0;
    border-radius: 35px;
}

.actions {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
}

.actions img {
    width: 20px;
    height: 20px;
}

@media (max-width: 650px) {
    .post {
        flex-flow: column nowrap;
        align-items: center;
    }

    .author-photo {
        width: 15%;
        height: 15%;
        border-radius: 50%;
    }

}

@media (max-width: 650px) {
    .post {
        padding: 10px 10px 25px 10px;
        background-color: rgb(29, 44, 58);
    }

    .author-photo {
        width: 22%;
        height: 22%;
    }
    
    .article {
        display: flex;
        flex-flow: column nowrap;
        align-items: flex-start;
        line-height: 20px;
    }

    .author-name {
        font-size: 14px;
    }
    
    .author-nickname, .date {
        font-size: 12px;
    }
     
    .post-text {
        font-size: 13px;
    }
    
    .article .post-photo {
        border-radius: 20px;
    }
    
    .actions img {
        width: 16px;
        height: 16px;
    }
}
.galery {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    margin: 15px 0 15px 0;
}

.galery img {
    width: 275px;
    margin: 10px 0 10px 0;
}
* {
    font-family: 'Roboto', sans-serif;
}

.contacts {
    display: flex;
    flex-flow: column nowrap;
    width: 300px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    border: 5px solid rgba(126, 126, 126, 0.779);
    border-radius: 0 15px 0 15px;
    padding: 5px;
    background-color: rgb(255, 255, 255);
}

.search-field {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

}

.search-field img {
    width: 16px;
    height: 16px;
    margin: 0 5px 0 5px
}

.search-field input {
    width: 80%;
    font-weight: 300;
}

.gender-filter {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    margin: 10px 0 20px 0;
    font-size: 15px; 
    text-transform: uppercase;
}

.gender-filter div {
    margin-right: 10px;
}


@media (max-width: 350px) {
    .contacts {
        width: 220px;
    }
}
.contact-item {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin: 15px 0 15px 5px;
}

.contact-item img, .contact-item div {
    margin: 0 5px 0 5px;
}

.contact-icon {
    width: 35px;
    height: 35px;
}

.contact-gender {
    width: 12px;
    height: 12px;
}

.contact-info {
    width: 60%;
    display: flex;
    flex-flow: column nowrap;
    font-size: 14px;
}

.contact-info span:first-child {
    font-size: 15px;
    font-weight: 600;
}

.contact-call {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

@media (max-width: 350px) {
    .contact-icon {
        width: 30px;
        height: 30px;
    }
    
    .contact-gender {
        width: 10px;
        height: 10px;
    }

    .contact-call {
        width: 20px;
        height: 20px;
    }

    .contact-info {
        width: 62%;
        font-size: 11px;
    }
    
    .contact-info span:first-child {
        font-size: 12px;
    }
}

